@media screen and (max-width: 767px) {
  .pricing0-wrapper {
    min-height: 720px;
  }
  .pricing0-wrapper .pricing0 {
    display: block;
  }
  .pricing0-wrapper .pricing0-img-wrapper {
    padding: 0;
    text-align: center;
    margin-top: 24px;
  }
  .pricing0-wrapper .pricing0-img-wrapper .pricing0-img {
